<template>
<section>
    <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" IdentityColumn="feature_id" :ListData="ModuleFeatureList" :HeaderData="Headers" :widgets="widgets" :showCheckBox="false" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions" ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" :SearchFields="leftSearchFields" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'Action'">
                <em class="fa fa-pencil text-success action-icon center-icon" v-on:click="Edit(data.row.enable_disable_sections_id)" :title="data.column.COLUMN_NAME"></em>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <td class="text-center single-action">
                    <div class="cstm-drop-btn">
                        <select v-model="data.row.status_id" @change="DrpdownUpdataStatus(data.row.status_id,data.row.enable_disable_sections_id);" :class="data.row.status_id==1001?'ddlupdatestatus w-80 btn-success' :'ddlupdatestatus w-80 btn-danger'">
                            <option value="1001" selected="selected">Active</option>
                            <option value="1002">Inactive</option>
                        </select>
                    </div>
                </td>
            </template>
        </template>
    </tg-list>
    <AddFeature v-if="IsAddFeature" :matchValue="matchValue"></AddFeature>
</section>
</template>

<script>
import DataService from "../../service/DataService";
import AddFeature from '../../views/AdvancedSetup/AddFeature';
import Schema from '../AdvancedSetup/ModuleFeatureSchema';

export default {
    components: {
        AddFeature
    },
    data() {
        return {
            leftSearchFields: Schema.leftsearchSchema,
            ModuleFeatureList: [],
            IsAddFeature: false,
            sortBy: '',
            sortExp: '',
            TotalRecords: 0,
            pageNumber: 1,
            pageSize: 10,
            CurrentPage: 1,
            Headers: [{
                    "COLUMN_NAME": "module_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('moduleName'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "sub_module_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SubModuleName'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "enable_disable_sections_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Section Name'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "status_id",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "feature_based_on",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BasedOn'),
                    "DISPLAY_ORDER": 5,

                },
                {
                    "COLUMN_NAME": "Action",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Action'),
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true
                    }
                },
            ],
            listheaderbuttons: [{
                title: 'Add Feature',
                iconClass: 'fa fa-plus',
                callbackfunction: 'AddModule',
                additionalClass: '',
                isdisabled: false,
                isvisible: true,
                checkPrivilege: false
            }],
            widgets: [],
            LegendArray: [],
            matchValue: {}
        }
    },
    created() {
        Schema.bindleftCommonSearchdropdown(this, 'Asset', 'Module_Name');
        Schema.bindleftCommonSearchdropdown(this, 'Asset', 'Sub_Module_Name');
        this.FetchData();
    },
    methods: {
        AddModule: function () {
            this.matchValue = {}
            this.IsAddFeature = true;
        },
        Edit: function (value) {
            this.matchValue = this.ModuleFeatureList.find(x => x.enable_disable_sections_id === value);
            this.IsAddFeature = true;
        },
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `feature_id=&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.pageSize}&pageNumber=${vueObj.pageNumber}&${vueObj.searchCondition}`;
            await DataService.GetEnableDisableSectionsListing(url).then(response => {
                vueObj.isLoading = false;
                if (response.data != null && response.data != 0) {
                    if (response.data.length > 0) {
                        vueObj.ModuleFeatureList = response.data;
                        vueObj.TotalRecords = response.data[0].Totals;
                        vueObj.TotalPages = Math.ceil(
                            vueObj.TotalRecords / vueObj.pageSize
                        );
                        vueObj.CurrentPage = vueObj.pageNumber;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.ModuleFeatureList = [];
                        vueObj.TotalRecords = 0;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.ModuleFeatureList = [];
                    vueObj.TotalRecords = 0;
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap();
                }, 100);
            })
        },
        renderActions: function () {},
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        DrpdownUpdataStatus(StatusID,ID) {         
            var vm = this;
            var StatusId = StatusID
            var Id = ID;
            vm.confirmR(vm.$t('Are you sure to update this record?'), true, false, function (result) {      
                var url = `id=${Id}&statusId=${StatusId}&tableName=Talygen_Enable_Disable_Sections&primaryKey=enable_disable_sections_id`
                DataService.UpdateStatus12(url).then(
                    response => {
                        console.log('res', response);
                        vm.FetchData();
                    }
                )
            })
            vm.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddModule":
                    this.AddModule();
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

    }
}
</script>
